.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(5, 5, 5, 0.8);
    z-index: 99999;
}

.modal {
    width: 300px;
    height: 266px;
    border-radius: 12px;
    background-color: var(--pr-input-background-color);
    padding: 16px 12px;
}